.Banner {
    width: 100%;
    height: 6vh;
    background-color: #000000;
}

.icon-container {
    width: 100%;
    height: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: left;
}

.icon-box {
    width: 2.444%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}


.icon-box:hover {
    background-color: #333333;
}

.Customize-logo {
    position: absolute;
    width: 80%;
    height: 80%;
}

.down-logo:hover {
    background-color: #333333;
}


.Banner-title-box {
    width: 75vw;
    height: 100%;
    display: flex;
    /* position: absolute; */
    align-items: center;
    justify-content: center;
    /* left: 854px; */
    padding: 10px;
    
}

.Banner-title-text-box {
    width: 15.2%;
    height: 50%;
    text-align: right;
}

.Banner-title-text {
    font-size: 100%;
    color: #FFFFFF;
}



.Customize-social {
    position: absolute;
    width: 128px;
    height: 32px;
    margin-top: -64px;
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
  
  }


