.Customize_Down_Menu {
    position: absolute;
    bottom: 0%;
    left: 13%;
    /* width: 1572px; */
    width: 85vw;
    height: 19.2%;
    background-color: rgba(0, 0, 0, 0.4);
    padding: 12px 24px 24px 24px;
    align-items: center;
    gap: 16px;
    display: flex;
    overflow-x: auto;
 }


 .scroll-item-x {
  display: inline-block;
 }

.Customize_Down_Menu_scene {
    width: 192px;
    height: 168px;
    position: relative;
}

.Customize_Down_Menu_scene.selected {
  background-color: rgb(125, 137, 248);
  border: 3px solid #7D89F8;
}


.Customize_Down_Menu_scene:hover {
    background-color: #333333;
}

.Customize_Down_Menu_scene.selected:hover {
  background-color: rgb(125, 137, 248); /* 선택된 상태의 배경색 유지 */
  border: 3px solid #7D89F8;
}

.Customize_Down_Menu_scene_top_box {
  width: 192px;
  height: 24px;
  padding: 8px 0px;
  gap: 8px;
  display: flex;
  
}

.Customize_Down_Menu_scene_time {
  width: 135px;
  height: 16px;
  gap: 10px;
  display: flex;
  background: #FFF;
  padding: 4px;
  align-items: center;
  
}

.Customize_Down_Menu_scene_time_input {
  color: #000000;
  font-size: 16px;
  text-align: left;
  width: 98px;
}


.Customize_Down_Menu_scene_time_unit {
  color: rgba(0, 0, 0, 0.20);
  font-size: 16px;
  text-align: right;
}

.Customize_Down_Menu_scene_number {
  color: #FFFFFF;
  font-size: 16px;
  letter-spacing: 0.64px;
  
}

.Customize_Down_Menu_scene_block {
  width: 192px;
  height: 128px;
  background-color: #FFF;
  top: 40px;
  position: absolute;
  display: flex;
}

.Customize_Down_Menu_moving_time_box {
  width: 48px;
  height: 128px;
  padding: 12px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.Customize_Down_Menu_moving_time_input {
  width: 48px;
  font-size: 16px;
  text-align: center;
  margin-top: 4px;
}

.white {
  color: #FFF;
}

.black {
  color: #000;
}

.Customize_Down_Menu_moving_time_unit {
  width: 48px;
  font-size: 16px;
  text-align: center;
  color: #FFF;
  
}