 

.Customize_Side_Menu_Player_Title {
    padding: 16px;
    color: #FFFFFF;
    font-size: 16px;
    text-align: center;
    font-family: Montserrat;
    background: #282828;
    border-bottom: 1px solid #272727;
}

.Customize_Side_Menu_Player {

    padding: 20px 16px;
    display: flex;
    align-items: center;
    gap: 8px;
    background-color: transparent;
    
}
 
.Customize_Side_Menu_Player:hover {
    background-color: rgba(255, 255, 255, 0.20);
    cursor: pointer; 
    transition: background-color 0.3s ease; 
}

.Customize_Side_Menu_Player_text {
    color: #FFFFFF;
    font-size: 16px;
    font-family: Montserrat;
}

.Customize_Side_Menu_Player_manuplate {
    width: 100%;
    height: 6%;
    display: flex;
    border-top: 1px solid rgba(255, 255, 255, 0.20);
    
}

.Customize_Side_Menu_Player_manuplate_icon_box {
    width: 48px;
    height: 60px;
    padding: 10px;
} 
